<script>
import { Doughnut } from "vue-chartjs";
import gradientColor from "@/utill/chartColor";
// eslint-disable-next-line no-unused-vars
import ChartDataLabels from "chartjs-plugin-labels";

export default {
  extends: Doughnut,
  props: {
    dataObj: Object,
  },
  watch: {
    dataObj() {},
  },
  data() {
    return {
      colorArr: [],
      valueArr: [12, 17, 15, 16, 17, 2, 3],
      labelArr: [
        "41 ~ 50kg",
        "51 ~ 60kg",
        "61 ~ 70kg",
        "71 ~ 80kg",
        "81 ~ 90kg",
        "161 ~ 170kg",
        "171 ~ 180kg",
      ],
    };
  },
  methods: {
    weightChartInit() {
      /*  for (let i of param.label) {
        this.labelArr.push(i);
      }
      for (let i of param.value) {
        this.valueArr.push(i);
      }*/
      this.chartInit();
    },
    chartInit() {
      const colorArr = gradientColor();

      for (let i of colorArr) {
        let color = this.$refs.canvas
          .getContext("2d")
          .createLinearGradient(0, 0, 0, 450);

        color.addColorStop(0, i.s0);
        color.addColorStop(0.5, i.s50);
        color.addColorStop(1, i.s100);

        this.colorArr.push(color);
      }

      this.renderChart(
        {
          labels: this.labelArr,
          datasets: [
            {
              // borderColor: "#62CA4D",
              pointBackgroundColor: "white",
              borderWidth: 1,
              pointBorderColor: "white",
              backgroundColor: this.colorArr,
              data: this.valueArr,
            },
          ],
        },
        {
          responsive: true,
          maintainAspectRatio: false,
          legend: {
            // display: false,
            position: "left",
          },
          plugins: {
            labels: {
              display: false,
              fontColor: "#363636",
              textShadow: true,
            },
          },
        }
      );
    },
  },
  mounted() {
    // console.log(gradientColor);
  },
};
</script>
